	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/getting-mvv-visa/index_ru.md absPath of file >>> MarkdownRemark","html":"<p>Я уже писала как <a href=\"/ru/life/documents-for-dutch-visa-apostille\">апостилировать документы для получения вида на жительство в Голландии</a>.</p>\n<p>А сейчас — о том, как получать въездную визу.</p>\n<p>Напомню, что посольство не принимает решение — давать визу или нет. Оно лишь исполняет решение, принятое IND, к которому обращается работадатель или агент на голландской стороне. Когда IND принимает положительное решение, оно информирует заявителя, а он в свою очередь сообщает новость вам. И можно прямо сразу бежать за визой.\n<excerpt/></p>\n<p>Всё “в компьютере”, так что о решении IND в посольстве узнают мгновенно. Но чтобы зря не ходить, можно уточнить о наличии решения IND по телефону. В Москве это +7 (495) 797-29-79 (звонить c 14:00 до 16:30). В качестве входных данных можно просто назвать свои фамилию и имя.</p>\n<p>Обращаться за визой нужно в том консульском округе, который указал заявитель. То есть если при подаче документов в IND было сказано, что вы будете получать визу в Москве, в посольстве Нидерландов в Санкт-Петербурге ничего о вашем деле знать не будут. Если всё-таки оказалось нужным поменять город, об этом следует сообщить IND. Но с учетом коммуникаций такая замена потребует нескольких дней.</p>\n<p>За визой нужно идти не в визовый центр, а в посольство. Предварительная запись не требуется, но удобнее всё-таки записаться на сайте <a href=\"https://nlmoscow2-new.embassytools.com/\">nlmoscow2-new.embassytools.com</a>. Достаточно заполнить один слот на всю семью.\nС собой нужны следующие документы (на каждого человека):</p>\n<ul>\n<li><strong>Заявление о выдаче визы</strong>\nЗаявление должно быть заполнено на голландском формуляре. Только на сайте посольства голландской версии нет :-) Поэтому все приходят с заполненными английскими вариантами и прямо там перед окошком переписывают заявление. Однако у читателей моего блога есть уникальная возможность избежать этого. Держите <a href=\"http://yadi.sk/d/Ii2dnNXiCU23Q\">голландский вариант заявления на получение визы MVV</a>. Я его скачала с сайта голландского посольства в каком-то азиатском государстве. Формуляр точно правильный, я подавала на нем заранее приготовленную анкету. В окошечке даже спрашивали, где мне удалось её скачать. А чтобы разобраться в голландском, полезно взять <a href=\"http://yadi.sk/d/WIlTKaIKCU23e\">такую же анкету на английском</a>, и сравнивать поля.</li>\n<li><strong>2 фотографии</strong> формата “на Шенген”</li>\n<li><strong>Загранпаспорт</strong></li>\n<li>Копия загранпаспорта</li>\n<li>Апостилированное и переведенное <strong>свидетельство о браке</strong>\nТолько одно :-)</li>\n<li>Ксерокопия апостилированного и переведенного свидетельства о браке</li>\n<li>Апостилированное и переведенное <strong>свидетельство о рождении</strong>\nСтрого говоря, при получении визы оно требуется только детям. Взрослым свидетельство о рождении нужно уже на территории Нидерландов при получении BSN (это что-то вроде российского ИНН). Но на всякий случай можно взять в посольство и его. Вдруг у них в базе будет ваш полный тезка, тогда по свидетельству вас идентифицируют.</li>\n<li>Ксерокопия апостлированного и переведенного свидетельства о рождении</li>\n</ul>\n<p>Виза готовится… несколько часов. Приняв все ваши документы, вам выдадут номерок и приглашение приходить после обеда в тот же день. И тогда уже отдадут назад паспорта с заполненной страничкой.</p>\n<p><img src=\"http://distilleryimage10.ak.instagram.com/74ee0194149011e3982f22000a9f3c23_7.jpg\" alt=\"image\"></p>\n<p>Это виза на 3 месяца, но не спешите пугаться. 3 месяца даётся на въезд. Предполагается что их достаточно для получения нидерландского вида на жительство (такая пластиковая карточка), a он, конечно, действует существенно дольше — в зависимости от вашего рабочего контракта. Кроме того, виза многократная. Если вдруг понадобится срочно прилететь в Россию, а вид на жительство ещё не готов, виза позволяет путешествовать туда-сюда неограниченно.</p>\n<p>Всё, можно бронировать билет на самолёт. Несмотря на то, что виза нацональная, а не Шенгенская, можно лететь с пересадкой, потому что тип D.</p>","fields":{"slug":"ru/life/getting-mvv-visa/","prefix":"","disqusIdentifier":"life-getting-mvv-visa-index-ru"},"frontmatter":{"title":"Процесс получения визы MVV (для работы в Голландии)","date":"11 November 2013","v2":true,"old":null,"tumblr":true,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/life/getting-mvv-visa/","prev":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/hello-from-the-netherlands/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/hello-from-the-netherlands/","prefix":"","lang":"ru","disqusIdentifier":"life-hello-from-the-netherlands-index-ru"},"frontmatter":{"title":"Привет из Голландии, фото"}},"next":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/from-finland-to-russia/index_en.md absPath of file >>> MarkdownRemark","fields":{"slug":"en/life/from-finland-to-russia/","prefix":"","lang":"en","disqusIdentifier":"life-from-finland-to-russia-index-en"},"frontmatter":{"title":"From Finland to Russia by car"}}}}

